import React from 'react'
import Container from './Container.js';

const Footer = (props) =>{
    const style={
        position: 'relative',
        width: '100px',
        height: '100%',
        textAlign: 'center',
        fontSize: '10px',
        bottom: '0',
    }
    return(
        <div style={style}>
            <Container>
                <p>&copy;2019 Lin Chen</p>
            </Container>
        </div>
    )
}

export default Footer;