import React, {Component} from 'react';
import { Route, BrowserRouter as Router, Switch, Link, Redirect, HashRouter } from 'react-router-dom'
import Profile from './Components/Profile/Profile'
import Projects from './Components/Projects/Projects'
import Footer from './Components/Footer'
import Header from './Components/Header/Header'
import './App.css'

class App extends Component{
  state = {
    navigators : [{label: 'Profile', link: '/profile'}, {label: 'Projects', link: '/projects'}, {label: 'Contact', link: '/contact'}],
    skills : ['Blender', 'Linux', 'Adobe Photoshop', 'Unity 3D', 'Unreal Engine 4'],
    languages : ['Python', 'C++', 'JavaScript (JQuary, ReactJS...)', 'HTML/CSS', 'Java', 'C#', 'Bash',],
    educations : [
      {
        name: 'New Jersey Institue of Technology',
        start: 2016,
        end: 2019,
        degree: 'Master of Science of Computer Science'
      },
      {
        name: 'Saint Peter\'s Univeristy',
        start: 2014,
        end: 2016,
        degree: ''
      }
    ],

    contactInfo : {
      email: 'lhchenlin@gmail.com',
      address: 'Bayonne, NJ 07002',
      githubUrl: 'https://github.com/lhchen42',
      linkedinUrl: 'https://www.linkedin.com/in/lin-chen-1021',
    },

    slides: [
      {
        name: 'img1',
        url: '/images/img1.jpg'
      },
      {
        name: 'img2',
        url: '/images/img2.jpg'
      }
    ],
    projects: [
      {
        name: 'Decision Tree-ID3&C4.5 Algorithm',
        description: 'Implementation of the Decision Tree Algorihtm, ID3 and C4.5. It able to build the decision tree from the training data',
        screenShots: ['/images/DecisionTree/ID3_continuous.png', 
                      '/images/DecisionTree/C4.5continuous.png',
                      '/images/DecisionTree/Mushroom.png',
                      '/images/DecisionTree/TTT_before_prune.png',
                      '/images/DecisionTree/TTT.png',
                      '/images/DecisionTree/output.png'
                     ]
      },
      {
        name: 'Planetarium',
        description: 'A VR environment of the solar system',
        screenShots: ['/images/Planetarium/img0.png',
                      '/images/Planetarium/img1.png',
                      '/images/Planetarium/img2.png',
                      '/images/Planetarium/img3.png',

        ]
      },
      {
        name: 'Clannad-Light Orbs',
        description: 'Dynamic desktop wallpaper, a mod for Wallpaper Engine',
        screenShots: ['/images/WE/img1.png', '/images/WE/img2.jpg', '/images/WE/img3.png', '/images/WE/img4.png'],
      },
      {
        name: 'DFA',
        description: 'An object-oriented problem that capable to build DFA',
        screenShots: ['/images/DFA/DFA_drawing.jpg']
      },
      {
        name: 'Exam System',
        description: 'CS490 Project. Frontend. A web applicatio that allow professors to create the exams and student to take the exam.',
        screenShots: ['/images/Exam/img0.png', '/images/Exam/img1.png', '/images/Exam/img2.png']
      }
      // {
      //   name: 'myproject2',
      //   screenShots: ['/images/img2.jpg'],
      // }
    ],
    viewProfile: true,
    viewPorject: true,
  };

  render(){
    let profile = null;
    if(this.state.viewProfile){
      profile = (<Profile 
        name="Lin Chen" 
        skills={this.state.skills} 
        languages={this.state.languages} 
        educationsInfo={this.state.educations}
        contactInfo={this.state.contactInfo}
        >
        I am passionate about learning the new technology and aspire to become a security software developer.
      </Profile>);
    }

    let projects = null;
    if(this.state.viewPorject){
      projects = (<Projects projects={this.state.projects}/>)
    }

    return (
      <div className="App">
        <HashRouter>
        <Header navigators = {this.state.navigators} />
          <Switch>
          <Route path="/projects">
            {projects}
          </Route>
          <Route path="/profile">
            {profile}
          </Route>
          <Redirect from="/" to="/profile" />
          </Switch>
        <Footer />
        </HashRouter>
      </div>
    );
  }
}

export default App;
